import Lottie from "react-lottie-player";
import mgram from "./assets/mgram.json";
import { useState } from "react";

function Lottie1() {
  const [direction, setDirection] = useState(1);
  const [speed, setSpeed] = useState(0.5);
  const mouseActions = (fn) => {
    setSpeed(0.3);
    setTimeout(() => {
      fn();
      setSpeed(0.5);
    }, 350);
  };
  return (
    <div>
      <Lottie
        background="transparent"
        loop
        speed={speed}
        direction={direction}
        onMouseEnter={() => mouseActions(() => setDirection(-1))}
        onMouseLeave={() => mouseActions(() => setDirection(1))}
        play
        controls
        animationData={mgram}
        style={{ width: 350, height: 250 }}
      />
    </div>
  );
}

export default Lottie1;
